<template>
  <div id="app">
    <div class="app-bg"></div>
    <div class="loader-page">
      <div class="trade-mark">
        <div class="trade-mark-txt">自由钢琴 <span class="trade-mark-txt--en">Auto Piano</span></div>
      </div>
      <div class="loader-progress">
        <h1><van-loading type="circle" color="black"></van-loading>
          <span>资源加载中</span></h1>
        <van-progress :percentage="percent" :pivot-text="percent + '%'"></van-progress>
      </div>
    </div>
    <router-view v-on:autoPianoLoadEvent="handleAutoPianoload"></router-view>
    <page-footer></page-footer>
  </div>
</template>

<script>
import Vue from 'vue';
import PageFooter from './components/Footer';
import { Loading } from 'vant';
import { Progress } from 'vant';

Vue.component(Progress.name, Progress);

Vue.component(Loading.name, Loading);

export default {
  name: 'app',
  components: {
    PageFooter
  },
  data() {
    return {
      percent: 0
    }
  },
  methods: {
    handleAutoPianoload(data) {
      var vm = this;
      vm.percent = data;
      if (data >= 100) {
        setTimeout(()=> {
          $('.loader-page').fadeOut()
        }, 700)
      }
    }
  }
}
</script>

<style lang="scss">
@import url('./assets/normalize.css');
$pblue: #1296db;
$textdark: #2c3e50;

::-webkit-scrollbar {  
  width: 0px;  
  height: 0px;  
  background-color: #F5F5F5;  
}  

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html, body {
  width: 100%;
  height: 100%;
  min-width: 1024px;
}
a {
  text-decoration: none;
  color: $textdark;
}
#app {
    width: 100%;
    min-width: 1024px;
    min-height: 100%;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: $textdark;
    padding: 1px;
    position: relative;

    .app-bg {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: -100;
      opacity: 0.5;
      background: url('../static/imgs/bg2.jpg') no-repeat;
      background-size: cover;
    }

    .loader-page {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1000;
      background: #fff;
      .trade-mark {
        width: 300px;
        height: 60px;
        line-height: 60px;
        padding-left: 50px;
        margin-left: 5%;
        background: url('../static/imgs/music_piano.png') no-repeat;
        background-size: 35px 35px;
        background-position: 0 center;
        .trade-mark-txt {
          font-size: 20px;
          font-weight: 500;
          color: $pblue;
          .trade-mark-txt--en {
            font-style: italic;
          }
        }
      }

      .loader-progress {
        width: 350px;
        height: 300px;
        margin: auto;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        text-align: center;

        h1 {
          font-size: 25px;
        }
        .van-loading {
          display: inline-block;
          margin-bottom: 5px;
          margin-right: 5px;
        }

        .van-progress {
          margin-top: 50px;
          .van-progress__pivot {
            width: 35px;
          }
        }
        
      }
    }
}
</style>



